.cr-homescene {

}

.cr-homescene-banner {
    background-color: black;
    padding-top: 32px;
    padding-bottom: 48px;
    padding-left: 48px;
    padding-right: 48px;
    color: white;
}

.cr-homescene-title {
    font-size: 48px;
    font-weight: bold;
    margin-bottom: 20px;
}

.cr-homescene-subtitle {
    /*font-weight: bold;*/
    opacity: 0.87;
    line-height: 1.6;
}

.cr-homescene-countdown {
    font-weight: bold;
    opacity: 0.87;
    line-height: 1.6;
    margin-top: 16px;
    font-size: 20px;
}

.cr-homescene-countdown-price {
    color: rgb(150, 150, 255);
}

.cr-homescene-countdown-time {
    color: green;
}

.cr-homescene-showroom {
    padding-top: 48px;
    padding-bottom: 64px;
}

.cr-homescene-marquee-img {
    width: 256px;
}

.cr-homescene-socials {
    padding-bottom: 64px;
    padding-left: 16px;
    padding-right: 16px;
}

.cr-homescene-info-title {
    font-size: 48px;
    font-weight: bold;
    margin-bottom: 16px;
    text-align: center;
}

.cr-homescene-discord-link {
    color: #7289DA;
    text-decoration: none;
}
.cr-homescene-discord-link:hover {
    text-decoration: underline;
}

.cr-homescene-twitter-link {
    color: #1DA1F2;
    text-decoration: none;
}
.cr-homescene-twitter-link:hover {
    text-decoration: underline;
}

@media (max-width: 480px) {
    .cr-homescene-title {
        font-size: 32px;
    }
    .cr-homescene-info-title {
        font-size: 32px;
    }
    .cr-homescene-marquee-img {
        width: 200px;
    }
}